import React,{useEffect,useState,useRef} from "react";
import moment from "moment";
import qs from "qs";
import axios from "axios";
import {dateFormat} from "../../lib/dateFormat";
import "antd/dist/antd.css";
import {v4 as uuidv4} from "uuid";
import ProTable from '@ant-design/pro-table';
import { PageContainer, FooterToolbar } from '@ant-design/pro-layout';
import {PlusOutlined,DeleteOutlined, EditOutlined } from '@ant-design/icons';
import {Button, Space, Tag,Popconfirm, message} from "antd";
import { statusColor } from "../../lib/statusTag";
import CreateForm from "./CRUDElements/CreateForm";
import UpdateForm from "./CRUDElements/UpdateForm";
import DetailsTable from "./CRUDElements/DetailsForm";

export default function FlightCRUD(){

    //Modal
    const [createModalVisible,handleCreateModalVisible] = useState(false);
    //ModalcreateModal
    const [updateModalVisible,handleUpdateModalVisible] = useState(false);
    const [detailModalVisible,handleDetailModalVisible] = useState(false);
    const [selectedRows,setSelectedRows] = useState([]);
    //
    const [stepFormValues, setStepFormValues] = useState({});
    //
    const [radioValue, setRadioValue] = useState('read');
    //api
    const [apiData, setApiData] = useState([]);
    //
    const actionRef = useRef();

    //, 
    const fetchFlightData = async (query)=>{
        return axios({
          url:"http://localhost:8080/airlineStaff/findAllFlights",
          method:"GET"
        }).then(function(response){
          if(response.data){
              console.log("");
              return {"data":{...response.data},"success":true};
          }else{
             return {"data":{},"success":false};
          }
        }).catch(function(){
          console.log("")
          return {"data":dataSource,"success":true};
        })  
       
    }

    const dataSource = {
      "records":[
        {
          flightNum: "MU12243234",
          airlineName:"Cathay Pacific",
          sourceAirportName:"PVG",
          departureTime:moment("2020-01-01").format("YYYY-MM-DD HH:mm:ss"),
          destAirportName:"SZX",
          arrivalTime:moment("2020-01-01").format("YYYY-MM-DD HH:mm:ss"),
          price:3030,
          status:"upcoming",
          airplaneId:"MU888"
        },
        {
          flightNum: "MU1224",
          airlineName:"Cathay Pacific",
          sourceAirportName:"PVG",
          departureTime:moment("2020-01-01").format("YYYY-MM-DD HH:mm:ss"),
          destAirportName:"SZX",
          arrivalTime:moment("2020-01-01").format("YYYY-MM-DD HH:mm:ss"),
          price:3030,
          status:"upcoming",
          airplaneId:"MU888"
        },
        {
          flightNum: "MU234",
          airlineName:"Cathay Pacific",
          sourceAirportName:"PVG",
          departureTime:moment("2020-01-01").format("YYYY-MM-DD HH:mm:ss"),
          destAirportName:"SZX",
          arrivalTime:moment("2020-01-01").format("YYYY-MM-DD HH:mm:ss"),
          price:3030,
          status:"upcoming",
          airplaneId:"MU888"
        },
        
      ],"total":12,"success":true};


    const handleDelete= (flight_id,airline_name)=>{
        console.log("delete");
    }

    const handleUpdate = (flight_info)=>{
        console.log("update");
    }

    const handleUpdateMany =()=>{
      
    }

    //
    const handleCreate = (values)=>{
        axios({
          url:"http://localhost:8080/airlineStaff/addNewFlight",
          method:"post",
          data:{...values,
            departureTime:new Date(moment(values.departureTime)),
            arrivalTime:new Date(moment(values.arrivalTime))}
        }).then(function(response){
          if(response.data==="success"){
            message.success({
              content: 'Insertion Completed',
              className: 'custom-class',
              style: {
                marginTop: '40vh',
              },
            });
            handleCreateModalVisible(false);
          }else{
            message.error({
              content: 'You have already inserted this airplane information!',
              className: 'custom-class',
              style: {
                marginTop: '40vh',
              },
            });
          }
        })
        
    }

    const handleDetails =()=>{
        console.log("show info");
    }

    const confirm = (e)=>{
        console.log(e);
        message.success("Click on Yes");
    }

    const cancel = (e)=>{
        console.log(e);
        message.error("Click on No");

    }


    // 
    const columns = [
        {
            title: "Flight Number",
            dataIndex:"flightNum",
            key:"flight",
            textWrap:"word-break",
            width:100,
            ellipsis:true,
            fixed:"left",
            
        },
        {
            title: "Airline Name",
            dataIndex:"airlineName",
            textWrap:"word-break",
            width:100,
            ellipsis:true,
            key:"airline"
        },
        {
            title: "Dept. Airport",
            dataIndex: "sourceAirportName",
            textWrap:"word-break",
            width:100,
            ellipsis:true,
            key: "dept"
        },
        {
            title: "Arri. Airport",
            dataIndex: "destAirportName",
            textWrap:"word-break",
            width:100,
            ellipsis:true,
            key: "arri"
        },
        {
            title: "Dept. Time",
            dataIndex: "departureTime",
            valueType:"dateTime",
            // textWrap:"word-break",
            width:150,
            ellipsis:true,
            key: "dept_time"
        },
        {
            title: "Arri. Time",
            dataIndex: "arrivalTime",
            valueType:"dateTime",
            key: "arri_time",
            width:150,
            // textWrap:"word-break",
       
            ellipsis:true,
        },
        {
            title: "Price",
            dataIndex: "price",
            hideInSearch:true,
            key: "price",
            textWrap:"word-break",
            width:100,
            ellipsis:true,
        },
        {
            title: "Airplane ID",
            dataIndex: "airplaneId",
            hideInSearch:true,
            key: "airplane_id",
            textWrap:"word-break",
            width:100,
            ellipsis:true,
        },
        {
            title: "Status",
            dataIndex: "status",
            hideInSearch:true,
            key: "status",
            render: tags => (
                <>
                    {
                        <Tag color={statusColor[tags]} key={tags}>
                            {tags.toUpperCase()}
                        </Tag>
                    }
                </>
            )
        },
        {
            title: "Action",
            key: 'action',
            hideInSearch:true,
            fixed:"right",
            render: (text, record) => (
                <Space size="middle">
                <Button onClick={()=>{
                  handleDetailModalVisible(true);
                  setStepFormValues(record);
                  console.log("view details")
                }}>Details</Button>
                <Button onClick={()=>{
                    handleUpdateModalVisible(true);
                    setStepFormValues(record);
                    console.log("edit");

                }} type={'primary'} size={'small'} >
                  <EditOutlined style={{fontSize: '15px'}} />
                </Button>
                <Popconfirm
                     title="Are you sure to delete this row?"
                    onConfirm={confirm}
                    onCancel={cancel}
                    okText="Yes"
                    cancelText="No"
                >
                <Button onClick={()=>{
                    console.log("");

                }} type={'primary'} size={'small'} danger >
                  <DeleteOutlined style={{fontSize: '15px'}} />
                </Button></Popconfirm>
              </Space>
        
            )
        }
    ]

    //  
    const getData = async (params) => {
        //  pageIndex  current
        console.log("getData");
        const query = {
            ...params,
            pageIndex: params.current
        };
        delete query.current;

        // 
        console.log("")
        const {data,success} = await fetchFlightData(query); //
        
        // 
        return {
            data: data.records,
            success,
            total: data.total,
        };
    };

    return (
        <div>
        <ProTable
            columns={columns}
            actionRef={actionRef}
            request={getData}
            rowKey={(record,index)=>index}
            scroll={{"y":300,x:1300}}
            form={{span:8}}
            size={"middle"}
            rowSelection={{
                onchange:(_,selectedRows)=>setSelectedRows(selectedRows),
            }}
            toolBarRender={() => [
            <Button key="3" type="primary" onClick={()=>{
                handleCreateModalVisible(true);

            }}>
                <PlusOutlined />
                Add Flight
            </Button>,
        ]}
        />

        {selectedRows?.length > 0 && (
        <FooterToolbar
          extra={
            <div>
               <a style={{ fontWeight: 600 }}>{selectedRows.length}</a> &nbsp;&nbsp;
            </div>
          }
        >
          { radioValue === 'read' && (<Button
            danger
            type="primary"
            onClick={async () => {
              await handleDelete(selectedRows);
              setSelectedRows([]);
              // @ts-ignore
              actionRef.current?.reloadAndRest();
            }}
          >
            
          </Button>)}
          { radioValue === 'upload'&&(<Button
            type="primary"
            onClick={async () => {
              await handleUpdateMany(selectedRows);
              setSelectedRows([]);
            }}
          >
            
          </Button>)}
        </FooterToolbar>
      )}
        

      <CreateForm onSubmit={async (value) => {
            // 
            const success = await handleUpdate(value);
            if (success) {
              //Modal
              handleUpdateModalVisible(false);
              //
              setStepFormValues({});
              //
              if (actionRef.current) {
                actionRef.current.reload();
              }
            }
          }}
          onCancel={() => handleCreateModalVisible(false)} 
          modalVisible={createModalVisible}
          handleCreateModalVisible={handleCreateModalVisible}
          handleCreate={handleCreate}
          className={"createForm"}>
        {/* <ProTable
          onSubmit={async (value) => {
            const success = await handleCreate(value);
            if (success) {
              handleCreateModalVisible(false);
              if (actionRef.current) {
                actionRef.current.reload();
              }
            }
          }}
          rowKey={["flight_id","airline"]}
          type="form"
          columns={columns}
          rowSelection={{}}
        /> */}
      </CreateForm>
         
      {stepFormValues && Object.keys(stepFormValues).length ? (
        <UpdateForm
        className="updateForm"
          onSubmit={async (value) => {
            // 
            const success = await handleUpdate(value);
            if (success) {
              //Modal
              handleUpdateModalVisible(false);
              //
              setStepFormValues({});
              //
              if (actionRef.current) {
                actionRef.current.reload();
              }
            }
          }}
          //update
          onCancel={() => {
              // Modal
            handleUpdateModalVisible(false);
            //
            setStepFormValues({});
          }}
          handleUpdateModalVisible={handleUpdateModalVisible}
          updateModalVisible={updateModalVisible}
          values={stepFormValues}
          handleUpdate={handleUpdate}
        />
      ) : null}

      {stepFormValues && Object.keys(stepFormValues).length ? (
        <DetailsTable className={"DetailForm"}
          handleDetailModalVisible={handleDetailModalVisible}
          detailModalVisible={detailModalVisible}
          onCancel={() => {
              // Modal
            handleDetailModalVisible(false);
            //
            setStepFormValues({});
          }}
          values={stepFormValues}
        ></DetailsTable>):null}
      
    </div>
        



    );
};
